.app-page-container {
    display: flex;
    flex-direction: column;
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.app-page-container-absolute {
    position: absolute;
    height: 100%;
    width: 100%;
}

.app-section {
    width: 100%;
    height: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app-section:nth-of-type(2){
    height: 50%;
    /* top: -1px; */
    padding-bottom: 1px;
}